import request from "utils/request";
import { deepClone } from 'utils';

//
const URL = {
    "GET_DETAIL": `${GROBAL_HTTP_CTX}/popup_allowances/list`, //search_id 
    "SAVE_ORDER": `${GROBAL_HTTP_CTX}/popup_allowances/insertSelective`, //
    "UPDATE_ORDER": `${GROBAL_HTTP_CTX}/popup_allowances/updateSelective`, //
    "DEL_ORDER": `${GROBAL_HTTP_CTX}/popup_allowances/deleteBatch`,
    "GET_LIST": `${GROBAL_HTTP_CTX}/popup_allowances/list`, //
}

/**
 * 
 * @param {*} params
 */
export const getList = (param) => {
    let newParam = Object.assign({}, param),
        pageParams = deepClone(newParam.pageParams);
    delete newParam.pageParams;
    return request(URL.GET_LIST, {
        method: "post",
        data: newParam,
        param: pageParams
    });
}

/**
 * table
 * @param {*} params
 */
export const deleteList = (params) => {
    return request(URL.DEL_ORDER, {
        method: "post",
        data: params
    });
}

/**
 * 
 * @param {*} params
 */

export const saveOrder = (params) => {
    return request(URL.SAVE_ORDER, {
        method: "post",
        data: params
    });
}

/**
 * 
 * @param {*} params
 */

export const updateOrder = (params) => {
    return request(URL.UPDATE_ORDER, {
        method: "post",
        data: params
    });
}



/**
 * search_id 
 */

export const getDetail = (param) => {
    return request(URL.GET_DETAIL, {
        method: "get",
        param
    });
}