import {actions} from "mirrorx";
// services
import * as api from "./service";
// 
import {processData} from "utils";


/**
 *          btnFlag
 *          
 *          
 *          
 *          012 3
 *async loadList(param, getState) {
 *          rowData
 */


export default {
    //  Store 
    name: "popupEdit",
    initialState: {
        rowPopData: {},
        showLoading: false,
        list: [],
        pageIndex: 0,
        pageSize: 25,
        totalPages: 1,
        total: 0,
        queryParam: {
            pageParams: {
                pageIndex: 0,
                pageSize: 25,
            },
            // groupParams: [],
            whereParams: []
        }
    },
    reducers: {
        /**
         *  Redux  Reducer
         * @param {*} state
         * @param {*} data
         */
        updateState(state, data) { //state
            return {
                ...state,
                ...data
            };
        }
    },
    effects: {
        /**
         * 
         * @param {*} param
         * @param {*} getState
         */
        async loadList(param, getState) {
            //  Loading 
            actions.popupEdit.updateState({showLoading: true});
            //  getList 
            const _param = param || getState().popupEdit.queryParam;
            const {result} = processData(await api.getList(_param));
            const {data:res}=result;
            let _state = {
                showLoading: false,
                queryParam: _param //
            }

            if (res) {
                _state = Object.assign({}, _state, {
                    list: res.content,
                    pageIndex: res.number + 1,
                    totalPages: res.totalPages,
                    total: res.totalElements,
                    pageSize: res.size,
                    rowPopData: (res.content.length > 0 ? res.content[0] : {}),
                })
            }
            actions.popupEdit.updateState(_state);
        },

        /**
         * table
         * @param {*} id
         * @param {*} getState
         */
        async removeList(param, getState) {
            actions.popupEdit.updateState({ showLoading: true });
            const {id} = param;
            const { result } = processData(await api.deleteList([{id}]),'');
            if (result.status === "success") {
                const state = getState().popupEdit;
                const { queryParam, list, totalPages } = state;
                //  getList 
                const { pageParams: { pageIndex } } = queryParam;
                if ( pageIndex > 0 && pageIndex + 1 === totalPages && list.length === 1) {
                    queryParam.pageParams.pageIndex = pageIndex - 1;
                }

                actions.popupEdit.loadList(queryParam);
            } else {
                actions.popupEdit.updateState({ showLoading: false});
            }
        },


        async saveOrder(param, getState) {//
            actions.popupEdit.updateState({showLoading: true});
            let status = null;
            const {btnFlag} = param;
            delete param.btnFlag; //
            if (btnFlag === 0) { // 
                let {result} = processData(await api.saveOrder(param), '');
                status=result.status;

            }
            if (btnFlag === 1) { // 
                let {result} = processData(await api.updateOrder(param), '');
                status = result.status;
            }
            if (status==="success") {
                actions.popupEdit.loadList();
            }else {
                actions.popupEdit.updateState({showLoading: false});
            }
        },

        // /**queryDetail
        //  *
        //  * @param param
        //  * @param getState
        //  * @returns {Promise<{}>}
        //  */
        // async queryDetail(param, getState) {
        //     let {result: {}} = processData(await api.getDetail(param));
        //     const {content: []} = result.data || [];
        //     return content[0] ? content[0] : {};
        // },

    }
};
